@import "/Users/Danny/dev/personal/savitreact/frontend/src/config/_variables.scss";
.form-component {
   margin-top: 5px;
   display: flex;
   flex-direction: column;
   margin-bottom: 25px;
   .form {
      display: flex;
      flex-direction: column;
      .button {
         margin-top: 10px;
      }
      .text-input {
         border: 1px solid #efefef;
         border-radius: 3px;
         background-color: $bg-color;
         margin-bottom: 7px;
         font-size: $normal-font-size;
         padding: 9px 0 7px 8px;
         // font-weight: 400;
         &:focus {
            outline: none;
            border: 1px solid #b2b2b2;
         }
      }
   }
   .signup-header {
      font-size: 17px;
      color: $dark-grey;
      font-weight: 600;
      margin-bottom: 20px;
   }

   .terms {
      width: 80%;
      margin: 0 auto;
      line-height: 20px;
      color: $dark-grey;
      span {
         font-weight: 600;
         cursor: pointer;
      }
   }

   .facebook-link {
      // align-items: center;
      color: #385185;
      // background-color: white;
      // justify-content: center;
      cursor: pointer;
      border: 0;
      font-size: $big-font-size;
      margin-bottom: 25px;

      i {
         margin-right: 5px;
      }
      // &:focus,
      // &:active {
      //    outline: none;
      // }
   }
}

.button {
   @include blueButton();
   i {
      font-size: 18px;
      margin-right: 5px;
   }
   svg {
      margin-right: 10px;
   }
}

.divider {
   margin: 14px 0 14px 0;
   text-transform: uppercase;
   color: $dark-grey;
   font-weight: 600;
   position: relative;
   &:before {
      content: "";
      width: 42%;
      position: absolute;
      background-color: $dark-grey;
      height: 1px;
      left: 0;
      top: 6px;
      opacity: 0.8;
   }
   &:after {
      content: "";
      width: 42%;
      position: absolute;
      background-color: $dark-grey;
      height: 1px;
      right: 0;
      top: 6px;
      opacity: 0.8;
   }
}
