@import "/Users/Danny/dev/personal/savitreact/frontend/src/config/_variables.scss";
.auth {
   width: 100%;
   height: 95vh;
   max-width: $max-page-width;
   margin: 0 auto;
   display: flex;
   min-height: 100%;
   justify-content: center;
   align-items: center;
   @include breakpoint("phone") {
      margin-top: 10px;
   }
}
.column {
   // width: 40%;

   @include breakpoint("phone") {
      &:first-child {
         display: none;
      }
   }
   @include breakpoint("tablet") {
      &:first-child {
         display: none;
      }
   }
   @include breakpoint("desktop") {
      .phone {
         width: 40vh;
      }
   }
   @include breakpoint("desktop-xl") {
      .phone {
         width: 45vh;
      }
   }

   // border: 1px solid red;
   &:last-child {
      max-width: 350px;
      width: 100%;
      margin-left: 35px;
      // background-color: blue;
      @include breakpoint("phone") {
         width: 100%;
         margin: 0;
      }
      @include breakpoint("tablet") {
         width: 70%;
         max-width: 350px;
         margin: 0;
      }
   }
   .app-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 35px;
      .appstores {
         margin-top: 25px;
         img {
            height: 40px;
            &:first-child {
               margin-right: 10px;
            }
         }
      }
   }
   .white-box {
      text-align: center;
      padding: 15px;
      @include boxBorder(no);
      @include breakpoint("phone") {
         background-color: transparent;
         border: 0;
      }
      .change-link {
         color: $light-blue;
         cursor: pointer;
      }
   }
   .form-box {
      margin-bottom: 10px;
      padding: 0 40px;
      padding-top: 30px;
      img {
         height: 60px;
      }
   }
}
