@import "/Users/Danny/dev/personal/savitreact/frontend/src/config/_variables.scss";
.footer {
   max-width: $max-page-width;
   width: 95%;
   margin: 0 auto;
   display: flex;
   align-items: center;
   justify-content: space-between;
   font-weight: 600;
   font-size: $small-font-size;
   text-transform: uppercase;
   flex-wrap: wrap;
   @include breakpoint(mobileonly) {
      justify-content: center;
   }
   .column {
      // margin-bottom: 15px;
      @include breakpoint(mobileonly) {
         margin-bottom: 0;
      }
      .nav {
         .list {
            display: flex;
            flex-wrap: wrap;
            @include breakpoint(mobileonly) {
               justify-content: center;
            }
            .list-item {
               margin-right: 10px;
               color: $dark-blue;
               cursor: pointer;
               @include breakpoint(mobileonly) {
                  margin-bottom: 15px;
               }
            }
         }
      }
      .copyright {
         color: $dark-grey;
      }
   }
}
