@import "/Users/Danny/dev/personal/savitreact/frontend/src/config/_variables.scss";
.comment-box {
   border-top: 1px solid $light-grey;
   padding: 15px 0;
   @include breakpoint("phone") {
      display: none;
   }

   .input {
      border: none;
      height: 20px;
      resize: none;
      width: 100%;
      font-size: $normal-font-size;
      &:focus {
         outline: none;
      }
      &::placeholder {
         color: $dark-grey;
      }
   }
}
